
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import SongService from "@/store/modules/song/service/song.service";
import ISong from "@/store/modules/song/interface/song.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect';

export default defineComponent({
  name: "SongsCreate",
  components: {
	  Multiselect
  },
  setup() {
    	const router = useRouter();
		const route = useRoute();

		const songService = new SongService();

		let file : any = null;

		const song = ref<ISong>({
			id: null,
			name: '',
			file: null,
			active: false
		});

		const getData = () => songService.edit(route.params.id)
		.then((response) => {
			response.data.active = (response.data.active == 1)
			song.value = response.data
		})
		.catch((error) => {
			Swal.fire(
			'Oh no!',
			'Información no encontrada',
			'error'
			);
			router.push({ name: "player-songs" });
		});

		const validateForm = () => {
			let disabled = false;
			if(song.value.name.length == 0) disabled = true;
			return disabled
		}

		const changeFile = (e: any) => {
          if(e.target.files.length > 0){
            file = e.target.files[0]
          }
        }

		const saveData = () => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea actualizar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Actualizar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						songService.update(song.value.id, groupData())
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información actualizada exitosamente',
							'success'
							).then(result => {
								router.push({ name: "player-songs" });
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se actualizaron los datos',
							'error'
							);
						});
					}
			})
			
		}

		const groupData = () => {
			let formData = new FormData();
			formData.append('file', file);
			formData.append('name', song.value.name);
			formData.append('active', song.value.active?'1':'0');
			return formData;
		}

		onMounted(() => {
			getData()
		});

		return {
			validateForm,
			changeFile,
			saveData,
			song
        };
  },
});
